@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
*,
*::after,
*::before {
  padding: 0;
  margin: 0;
  max-width: 100%;
  position: relative;
  box-sizing: border-box;
}

html {
  position: relative;
  min-height: 100%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #202020;
}

* {
  font-family: "Poppins", sans-serif;
}

.App {
  position: relative;
}

header {
  padding: 10px;
  color: white;
  text-align: center;
}

header > * {
  padding: 10px;
}

.modal {
  position: absolute;
  top: 50vh;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 40%;
  padding: 1.5rem;
  border-radius: 10px;
  text-align: center;
  box-shadow: 0 3rem 3rem rgba(0, 0, 0, 0.3);
  z-index: 10;
  background: linear-gradient(45deg, #0d324d, #7f5a83);
  color: white;
  font-size: 1.5rem;
  font-weight: 500;
}

.modal > * {
  margin: 10px;
}

.hidden {
  display: none;
}

.btn {
  background-color: #f3f3f3;
  padding: 10px;
  border: none;
  font-size: 16px;
  border-radius: 5px;
  cursor: pointer;
  outline: none;
}

.btn:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
  transition: all 0.3s;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  -webkit-backdrop-filter: blur(3px);
          backdrop-filter: blur(3px);
  z-index: 5;
}

footer {
  font-size: 14px;
  padding: 5px;
  position: fixed;
  width: 100%;
  bottom: 0;
  left: 0;
  right: 0;
  background: #393939;
  color: white;
  text-align: center;
}

a {
  text-decoration: none;
  color: orangered;
}

@media only screen and (max-width: 1000px) {
  .modal {
    font-size: 1.2rem;
    padding: 1rem;
  }

  .btn {
    font-size: 15px;
  }
}

@media only screen and (max-width: 768px) {
  .modal {
    width: 60%;
  }
}

@media only screen and (max-width: 480px) {
  .modal {
    font-size: 1.1rem;
    width: 75%;
  }

  .btn {
    font-size: 14px;
  }
}

.cards__grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-gap: 3rem;
  gap: 3rem;
  padding: 1rem;
  padding-bottom: 2.5rem;
}

.card {
  border: 10px solid goldenrod;
  border-radius: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  background: linear-gradient(#0d324d, #7f5a83);
}

.card:hover {
  cursor: pointer;
  box-shadow: 0 0 1.5rem goldenrod;
}

.name {
  background: goldenrod;
  width: 100%;
  text-align: center;
  padding: 5px;
  font-size: 18px;
}

.image {
  height: 200px;
  padding: 10px;
  object-fit: cover;
}

@media only screen and (max-width: 900px) {
  .cards__grid {
    grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
    grid-gap: 2rem;
    gap: 2rem;
  }

  .card {
    font-size: 16px;
  }

  .image {
    height: 150px;
  }
}

@media only screen and (max-width: 480px) {
  .cards__grid {
    grid-template-columns: repeat(auto-fill, minmax(125px, 1fr));
    grid-gap: 1rem;
    gap: 1rem;
  }

  .card {
    font-size: 14px;
  }

  .image {
    height: 125px;
  }
}

.score {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  color: white;
  line-height: 1.6rem;
  font-size: 20px;
  text-align: center;
  color: goldenrod;
}

.loader {
  position: absolute;
  top: 50vh;
  left: 50%;
  -webkit-transform: translate(-50%, -30vh);
          transform: translate(-50%, -30vh);
  z-index: 2;
  font-size: 2rem;
  color: white;
  text-align: center;
}

.loader img {
  height: 100px;
  padding: 10px;
  object-fit: cover;
  -webkit-animation: load 3s linear infinite;
          animation: load 3s linear infinite;
}

@-webkit-keyframes load {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes load {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

